import React, {useState} from "react"
import "./style.css"
import {uploadFile} from "../../cloudinary"
import {i18n} from "../../i18n/language"
import {chain} from "lodash"
import {isAnnotationInTime} from "../annotator/mark"
import Button from "../../elements/button/button"
import LoadingButton from "../../elements/loadingbutton";

const GallerySelect = ({images, onAnnotationSelect, cur, filter, folder, children, onNewImage, onChange, current, imageChildren}) => {

    const [state, setState] = useState(false)

    var url = `https://phantomjscloud.com/api/browser/v2/ak-maw28-drn6m-zchk2-wx8yy-c5zgk/?request={url:"https://beta.videobakers.de/collaboration/all/?workspace=${cur.public_id}",renderType:"pdf",renderSettings:{pdfOptions:{ format: "A4",
landscape:true,
preferCSSPageSize:true,
 }}}`;

    return <div className={"mygallery"}>
        <div className={"selection"} id={"galleryselection"}>
            {images.length >= 1 && <div className={"filter"}>

                {localStorage.getItem("videobakersadmin") === "true" && <LoadingButton promise={() => uploadFile({files: [url]}, (a) => {
                    window.open(a.url, "_blank");
                    return a;
                }, "temp_download")
                }>Kommentare herunterladen</LoadingButton>}

                {localStorage.getItem("videobakersadmin") === "true" && <select onChange={(e) => {
                    window.location = "/collaboration/file/?workspace=" + new URLSearchParams(window.location.search).get("workspace") + "&lang=" + e.target.value;

                }} value={new URLSearchParams(window.location.search).get("lang") || 'de'}>
                    <option value="de">Deutsch</option>
                    <option value="en">Englisch</option>
                </select>}

                <Button onClick={() => {
                    window.open(cur.secure_url.replace('/upload/', '/upload/fl_attachment/'), "_blank")
                }}>
                    Datei Herunterladen
                </Button>
            </div>}
            {images.map((i, idx) => {
                var timeAnnotations = i.annotations.filter(i => i.time > 0)

                var t = chain(timeAnnotations).groupBy("time").mapValues(v => chain(v).map("comment").flattenDeep())
                    .forEach(a => console.log(a))

                return <div key={i.public_id} id={`${i.public_id}_as`} className={"mygalleryimage"}>
                    <img alt="" key={idx} className={current(i) ? "selected" : ""}
                         onClick={() => onChange(i)} src={i.src}/>
                    {i.annotations.length > 0 && <p className={"badgeimage"}>{i.annotations.length}</p>}


                    {current(i) && timeAnnotations.length > 0 && <div className={"comments"}>
                        {timeAnnotations.map(annotation => {
                            return <div key={annotation.comment}
                                        className={`onecomment ${isAnnotationInTime(annotation, document.getElementsByTagName("video")[0]) ? "activecom" : ""}`}
                                        onClick={() => {
                                            onAnnotationSelect(annotation)
                                        }}>{i18n.annotator.second} {Math.round(annotation.time)}: {annotation.comment}</div>
                        })}
                    </div>}

                </div>
            })}
        </div>
        <div className={"gallerydetail"}>
            {children}
        </div>
    </div>
}

export default GallerySelect