import React, {useEffect, useState} from "react"
import "./style.css"
import Button from "../../../elements/button/button"
import TextareaAutosize from "react-autosize-textarea"
import {uploadFile} from "../../../cloudinary"
import {i18n} from "../../../i18n/language"
import Linkify from 'react-linkify';

const Mark = ({pos, getPane, remove, annotation, onMove, comment, onSave, onChange}) => {

    const [show, setShow] = useState(isSmartphone() ? !comment : true)
    const [focus, setFocus] = useState(false)

    function MarkFileUpload() {
        return <input accept="image/x-png,image/gif,image/jpeg" onChange={(e) => {
            uploadFile(e.target, (link) => {
                onChange(link)
                onSave()
                setShow(!isSmartphone())
            }, null, null)
        }} type={"file"}/>
    }

    function MarkImage() {
        return <img alt={"annotation image"} onClick={() => window.open(comment, "_blank")} src={comment}/>
    }


    /*%7Burl%3A%22https%3A%2F%2Fbeta.videobakers.de%2Fcollaboration%2Fall%2F%3Fworkspace%3D0.4461188197635124%2F0.4461188197635124-page-0000_rtiufe%22%2CrenderType%3A%22pdf%22%2CrenderSettings%3A%7Bviewport%3A%7Bwidth%3A1920%2Cheight%3A1080%7D%2CclipRectangle%3A%7Bwidth%3A1920%2Cheight%3A1080%7D%2CpdfOptions%3A%7B%20format%3A%20%22A4%22%2C%0Alandscape%3Atrue%2C%0ApreferCSSPageSize%3Atrue%2C%0A%20%7D%7D%7D*/
    function MarkBinIcon() {
        return <img className={"binicon"}
                    onClick={(e) => {
                        remove(e);
                    }}
                    src="data:image/svg+xml;base64,"
                    alt={"remove comment icon"}/>
    }

    function MarkTimestamp() {
        return <a onClick={() => getPane().currentTime = annotation.time} className={"timestamp"}>
            {i18n.annotator.second} {Math.floor(annotation.time * 100) / 100}
        </a>
    }

    function MarkSaveButton() {
        return <Button tagName={"button"} onClick={(e) => {
            onSave()
            setShow(!isSmartphone())
        }}>Speichern</Button>
    }

    function MarkIcon() {
        return <div draggable={true}
                    onDrag={(e) => {
                        e.nativeEvent.target.parentElement.style.top = e.nativeEvent.pageY - getPane().getBoundingClientRect().top + "px"
                        e.nativeEvent.target.parentElement.style.left = e.nativeEvent.pageX - getPane().getBoundingClientRect().left + "px"
                    }}
                    onDragEnd={(e) =>
                        onMove({
                            x: e.nativeEvent.pageX - getPane().getBoundingClientRect().left,
                            y: e.nativeEvent.pageY - getPane().getBoundingClientRect().top,
                        })
                    } className={"annoicon"}
                    onClick={() => setShow(!show)}/>
    }

    useEffect(() =>
        /*https://stackoverflow.com/questions/36308460/why-is-clientx-reset-to-0-on-last-drag-event*/
        document.addEventListener("dragover", (e) => e.preventDefault(), false), [])

    var printMode = window.location.href.indexOf("/all") !== -1;
    var leftShift = Math.min(125, (pos.x - 15))
    var rightShift = Math.max((140 - (getPane().offsetWidth - pos.x)), 0)
    var translateX = ((leftShift + rightShift) * -1) + "px"
    var tranlsateY = pos.y >= (getPane().offsetHeight / 2) ? "-100%" : 0

    return <div style={{top: pos.y, left: pos.x}}
                className={`${!comment ? "emptytext" : ""} annotation ${focus ? "hasFocus" : ""} ${annotation.time ? (isAnnotationInTime(annotation, getPane()) ? "visibletime" : "hiddentime") : ""}`}>

        <MarkIcon/>

        <div style={{"display": (show ? "block" : "none"), "transform": `translate(${translateX},${tranlsateY})`}}
             className={"arrow_box"}>


            {!printMode && <TextareaAutosize className={"marktextarea"} required={true}
                                             autoFocus={!comment}
                                             onBlur={() => {
                                                 !!comment && onSave()
                                                 setTimeout(() => setFocus(false), 100)
                                             }}
                                             value={comment}
                                             onFocus={() => setFocus(true)}
                                             onChange={(e) => onChange(e.target.value)} name="comment"
                                             placeholder={i18n.annotator.type}>{comment}</TextareaAutosize>}

            {printMode && <div className={"marktextarea"}><Linkify>{comment}</Linkify></div>}

            {!comment && <MarkFileUpload/>}

            {annotation.time && !!comment && <MarkTimestamp/>}

            <MarkSaveButton/>

            {!printMode && <MarkBinIcon/>}
        </div>
    </div>
}

export default Mark

function isSmartphone() {
    return navigator.userAgent.match(/Android/i)
        || navigator.userAgent.match(/webOS/i)
        || navigator.userAgent.match(/iPhone/i)
        || navigator.userAgent.match(/iPad/i)
        || navigator.userAgent.match(/iPod/i)
        || navigator.userAgent.match(/BlackBerry/i)
}

export function isAnnotationInTime(annotation, pane) {
    if (!pane) {
        return false
    }
    var diff = Math.round((pane.currentTime - annotation.time))
    return diff <= 2 && diff >= 0
}