class FilePage extends React.Component {

    state = {
        images: [],
        current: {
            "resource_type": "image",
            "src": `https://media.giphy.com/media/3oEjI6SIIHBdRxXI40/giphy.gif`,
            "annotations": [],
        },
    }


    componentDidMount() {

        if (typeof window === "undefined") {
            return;
        }

        document.onkeydown = (e) => {
            e = e || window.event

            var curIdx = this.state.images.indexOf(this.state.images.filter(i => i.public_id === this.state.current.public_id)[0])

            if (e.keyCode == "38") {
                this.loadImage(curIdx === 0 ? this.state.images[(this.state.images.length - 1)] : this.state.images[(curIdx - 1)])
            } else if (e.keyCode == "40") {
                this.loadImage(this.state.images[(curIdx + 1) % this.state.images.length])
            }


        }

        firebase.initializeApp(firebaseConfig);

        getFiles((images) => {

            this.setState({
                images: images,
                all_images: images,
            });

            var selectedImage = images.filter(i => i.public_id === new URLSearchParams(window.location.search).get("workspace"))

            this.loadImage(selectedImage.length >= 1 ? selectedImage[0] : images[0])


        })


    }

    loadImage(image) {
        this.setState({current: image, time: null})
    }

    render() {
        return (
            <IllustrationBackground>

                <GallerySelect
                    filter={(filter) => this.setState({
                        images: this.state.all_images.filter(i => i.annotations.length >= (filter ? 0 : 1)),
                    })}
                    onAnnotationSelect={(annotation) => {
                        document.getElementById(`${this.state.current.public_id}_img`).currentTime = annotation.time
                        this.forceUpdate()
                    }}
                    folder={typeof window !== "undefined" && new URLSearchParams(window.location.search).get("workspace").split("/")[0]}
                    current={e => this.state.current ? e.public_id === this.state.current.public_id : false}
                    onChange={(e) => this.loadImage(e)}
                    cur={this.state.current}
                    images={this.state.images}>

                    <ImageAnnotator
                        setAnnotations={(annos) => {
                            // eslint-disable-next-line
                            this.state.current.annotations = annos
                            this.forceUpdate()
                        }}
                        setTime={(currentTime) => this.setState({time: currentTime})}
                        time={this.state.time}
                        save={(id, annos) => {
                            this.state.current.annotations = this.state.current.annotations.filter(i => !!i.comment)
                            this.state.current.annotations.forEach(i => i.id = Math.random())
                            this.state.current.update(this.state.current.annotations)
                            this.forceUpdate()
                        }}
                        id={this.state.current.public_id}
                        image={this.state.current}>
                    </ImageAnnotator>
                </GallerySelect>
            </IllustrationBackground>
        )
    }
}

export default FilePage


function updatemeta(cur) {

    console.log(JSON.stringify({
        "meta": cur.annotations,
        "public_id": cur.public_id,
        resource_type: cur.resource_type,
    }))


    var firebaseT = require('firebase/app');
    require('firebase/auth');
    require('firebase/database');

    firebaseT.database().ref('files/' +(new URLSearchParams(window.location.search).get("lang") || 'de') + "/" +  cur.public_id).set({
        meta: JSON.parse(JSON.stringify(cur.annotations)),
    });


}

export function getFiles(onLoad, transformVideo) {

    var public_id = new URLSearchParams(window.location.search).get("workspace").split("_idx_")[0];

    var firebaseT = require('firebase/app');
    require('firebase/auth');
    require('firebase/database');

    firebaseT.database().ref('/files/' + (new URLSearchParams(window.location.search).get("lang") || 'de') + "/" + public_id).once('value').then((snapshot) => {
        fetch("/api/all?folder=" + public_id)
            .then(res => res.json())
            .then(all => {


                if (all.resources.length <= 0) {
                    Toastify({text: "There are currently no files in this workspace. It may take some time till all files are present after upload. Try again in 2 Minutes."}).showToast({duration: 60 * 1000})
                    return
                }


                var images = Array.from(all.resources).map(i => {
                    i.src = i.secure_url.replace(".mp4", ".jpg")
                    i.annotations = (snapshot.val() && snapshot.val().meta) ? snapshot.val().meta : []
                    i.annotations.forEach(i => i.id = Math.random())
                    i.annotations = i.annotations.sort(function(a, b) {
                        return a.time - b.time;
                    });
                    i.update = (annotations) => {
                        i.annotations = annotations
                        updatemeta(i)
                    }
                    return i
                })

                images = images.flatMap(file => {
                    console.log(file.pages >= 1)
                    if (file.pages >= 1) {
                        var imagesSplit = Array.from(Array(file.pages).keys()).map(id => {
                            var idx = id + 1
                            return {
                                ...file,
                                public_id: `${file.public_id}_idx_${idx}`,
                                src: file.secure_url
                                    .replace("https://res.cloudinary.com/hvioxpubt/image/upload/", `https://res.cloudinary.com/hvioxpubt/image/upload/pg_${idx}/`)
                                    .replace(".pdf", ".jpg"),
                                annotations: file.annotations.filter(i => i.page === idx),
                                update: (annotations) => {
                                    file.annotations = file.annotations
                                        .filter(annotation => annotation.page !== idx)
                                        .concat(annotations.map(i => {
                                            return {...i, page: idx}
                                        }))
                                    console.log(file)
                                    updatemeta(file)
                                },
                            }
                        })
                        return imagesSplit
                    } else {
                        return [file]
                    }
                })


                if (transformVideo) {
                    console.log(images);
                    images = images.flatMap(file => {
                        console.log("transofrming videos")
                        if (file.resource_type === "video") {
                            return file.annotations.map(annotation => {
                                var url = `https://res.cloudinary.com/hvioxpubt/video/upload/so_${annotation.time},eo_${annotation.time}/${file.public_id}.jpg`
                                return {
                                    public_id: (file.public_id + annotation.time + annotation.x + annotation.y),
                                    secure_url: url,
                                    src: url,
                                    resource_type: "image",
                                    annotations: [{...annotation, time: null}],
                                }
                            })
                        }

                        return [file]
                    })
                }
                console.log(images);
                //images.resources.sort(function(a, b){return a.time - b.time});

                onLoad(images)
            })
    })
}