import React from "react"
import Mark from "./mark"
import "./style.css"
import ResizeObserver from "resize-observer-polyfill"

class ImageAnnotator extends React.Component {

    componentDidMount() {
        this.definePlaying()
        this.addResizeListener()
        this.forceUpdate()//Erst wird das Bild gerendered, dannach knnen erst die Annotations geredendered werden.
    }

    componentWillReceiveProps(nextProps, nextContext) {
        this.setState({img: nextProps.image})
        setTimeout(() => this.forceUpdate(), 0)
    }


    /**
     * Gets current Pane for the annotations
     * @returns {boolean|HTMLElement}
     */
    getPane() {
        return typeof document !== "undefined" && document.getElementById(`${this.props.id}_img`)
    }

    /**
     * Defines the property playing for video elements. Playing is true if the video is currently playing.
     */
    definePlaying() {
        Object.defineProperty(HTMLMediaElement.prototype, "playing", {
            configurable: true,
            get: function () {
                return !!(this.currentTime > 0 && !this.paused && !this.ended && this.readyState > 2)
            },
        })
    }

    /**
     * Adds a Resize listenere to the container so that all marks changes the position according to the new size
     */
    addResizeListener() {
        new ResizeObserver(() => {
            this.forceUpdate()
        }).observe(this.getPane())
    }

    onPaneClick(e) {

        if (this.getPane().currentTime > 0) {//Nur wenn ein video ist soll das gemacht werden, beim erstellen video pasuieren
            e.preventDefault()
            this.getPane().pause()
        }


        if (this.getPane().currentTime === 0) { //der erste klcik soll zum play fhren und nicht dazu das eine makierung erstellt wird
            return
        }

        this.addAnnotation(this.newAnnotation(e.nativeEvent));
    }

    addAnnotation(anno){
        this.props.setAnnotations(this.props.image.annotations
            .filter(i => !!i.comment)
            .concat(anno));
    }

    newAnnotation(event) {
        return {
            x: event.offsetX / this.getXScale(),
            y: event.offsetY / this.getYScale(),
            time: this.getPane().currentTime,
            id: Math.random(),
        }
    }

    getXScale() {
        return this.getScale("Width")
    }

    getYScale() {
        return this.getScale("Height")
    }

    getScale(type) {
        var image = this.getPane()
        return image["offset" + type] / (image["natural" + type] || (image["video" + type] === 0 ? 1 : image["video" + type]))
    }

    render() {
        return [<div style={this.props.style} className={"annocontainer"} id={"annotationcontainer"}>

            {(!!this.props.time) && <div className={"videotimestamp"}>{this.props.time}</div>}

            {this.props.image.resource_type === "video" ?
                <video crossOrigin="anonymous" onTimeUpdate={() => this.props.setTime(this.getPane().currentTime)}
                       alt="curimg"
                       id={`${this.props.id}_img`}
                       controls={true}
                       onClick={(e) => this.onPaneClick(e)}
                       src={this.props.image.secure_url}
                       poster={this.props.image.src}/> :
                <img alt={`${this.props.id}_img`} id={`${this.props.id}_img`}
                     onClick={(e) => this.onPaneClick(e)}
                     src={this.props.image.src}/>
            }


            {this.getPane() && this.props.image.annotations.map((anno, idx) => {
                return <Mark key={anno.id}
                             getPane={() => this.getPane()}
                             remove={() => {
                                 anno.comment = ""
                                 this.props.save()
                             }}
                             onSave={() => this.props.save()}
                             onMove={(newPos) => {
                                 newPos.x = Math.max(newPos.x, 15) //Mindestens 15 Pixel zur Linken Seite abstand
                                 newPos.y = Math.max(newPos.y, 15) //Mindestnes 15 Pixel zur Obereren Seite abstand
                                 newPos.x = Math.min(newPos.x, this.getPane().offsetWidth - 15) //Mindesten 15 Pixel nach Rechts abstand
                                 newPos.y = Math.min(newPos.y, this.getPane().offsetHeight - 15) // Mindestens 15 Pixel nach Uten abstande
                                 anno.x = newPos.x / this.getXScale(this.getPane()) // Berechne die Absolute Position auf dem Pane
                                 anno.y = newPos.y / this.getYScale(this.getPane())// Berechne die Absolute Position auf dem Pane
                                 this.props.save()
                             }}
                             onChange={e => {
                                 anno.comment = e
                                 this.forceUpdate()
                             }} comment={anno.comment}
                             annotation={anno}
                             pos={{x: anno.x * this.getXScale(), y: anno.y * this.getYScale()}}/>
            })}


        </div>,]

        //<input
        //                 hidden={true}
        //                 onKeyPress={event => {
        //                     if (event.key === 'Enter') {
        //                         this.addAnnotation({
        //                             x: 0,
        //                             y: 0,
        //                             comment: event.target.value,
        //                             time: this.getPane().currentTime,
        //                             id: Math.random(),
        //                         })
        //                     }
        //                 }}
        //                 placeholder={"Bitte Geben Sie Hier ihren Kommentar fr dieses Folie ein. Drcken Sie dann Enter."} type={"text"} className={"textinput"}>
        //
        //             </input>
    }
}

export default ImageAnnotator

